<template>
  <div>
    <div class="content">
      <router-view :changeTitle="change"></router-view>
    </div>
  </div>
</template>

<script>
import Header from "@/components/header";
export default {
  name: "user",
  data() {
    return {
      title: "个人中心"
    };
  },
  components: {
    Header
  },
  updated(){
      this.title = '个人中心';
  },
  methods: {
    change(a) {
      this.title = a;
    }
  },
  mounted(){
    //console.log(sessionStorage.getItem("userInfo"));
  }
};
</script>

<style scoped>
</style>


